import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  Row,
  Col,
  ListGroup,
  Image,
  Form,
  Button,
  Card,
} from "react-bootstrap";
import Message from "../components/Message";
import { addToCart, removeFromCart } from "../actions/cartActions";
import { useTranslation } from "react-i18next";

function CartScreen({ match, location, history }) {
  const { t } = useTranslation();
  const productId = match.params._id;
  const qty = location.search ? Number(location.search.split("=")[1]) : 1;

  const dispatch = useDispatch();

  const cart = useSelector((state) => state.cart);
  const { cartItems } = cart;

  useEffect(() => {
    if (productId) {
      dispatch(addToCart(productId, qty));
    }
  }, [dispatch, productId, qty]);

  const removeFromVartHandler = (id) => {
    dispatch(removeFromCart(id));
  };

  const checkoutHendler = () => {
    history.push("/login?redirect=shipping");
  };

  return (
    <div className="margin-top-from-navbar">
      <Row>
        <Col md={8}>
          <h1>{t("CartScreen_title")}</h1>
          {cartItems.length === 0 ? (
            <Message variant="info">
              {t("CartScreen_empty_cart_message")}{" "}
              <Link to="/"> {t("CartScreen_btn_go_back")}</Link>
            </Message>
          ) : (
            <ListGroup variant="flush">
              {cartItems.map((item) => (
                <ListGroup.Item key={item.product}>
                  <Row className="CartScreenRow">
                    <Col md={2}>
                      <Image src={item.image} alt={item.name} fluid rounded />
                    </Col>
                    <Col md={3}>
                      <Link to={`/product/${item.product}`}>{item.name}</Link>
                    </Col>
                    <Col md={2}>${item.price}</Col>
                    <Col md={3}>
                      <Form.Control
                        as="select"
                        value={item.qty}
                        onChange={(e) =>
                          dispatch(
                            addToCart(item.product, Number(e.target.value))
                          )
                        }
                      >
                        {[...Array(item.countInStock).keys()].map((x) => (
                          <option key={x + 1} value={x + 1}>
                            {x + 1}
                          </option>
                        ))}
                      </Form.Control>
                    </Col>

                    <Col md={1}>
                      <Button
                        type="button"
                        variant="light"
                        onClick={() => removeFromVartHandler(item.product)}
                      >
                        <i className="fas fa-trash"></i>
                      </Button>
                    </Col>
                  </Row>
                </ListGroup.Item>
              ))}
            </ListGroup>
          )}
        </Col>

        <Col md={4}>
          <Card>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h2>
                  {t("CartScreen_empty_subtotal_items")} (
                  {cartItems.reduce((acc, item) => acc + item.qty, 0)})
                </h2>
                $
                {cartItems
                  .reduce((acc, item) => acc + item.qty * item.price, 0)
                  .toFixed(2)}
              </ListGroup.Item>
            </ListGroup>

            <ListGroup.Item variant="flush">
              <div className="d-grid gap-2">
                <Button
                  type="button"
                  className="bnt-block bg-brown rounded"
                  disabled={cartItems.length === 0}
                  onClick={checkoutHendler}
                >
                  {t("CartScreen_proceed_to_checkout")}
                </Button>
              </div>
            </ListGroup.Item>
          </Card>
        </Col>
      </Row>
    </div>
  );
}

export default CartScreen;
